import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import Typography from "@/components/typography";
import image from "@/assets/img-para-que-serve.svg";

import styles from "./index.module.css";

const query = graphql`
  query {
    site {
      siteMetadata {
        appSiteUrl
      }
    }
  }
`;

const ParaQueServe = () => {
  const data = useStaticQuery(query);
  return (
    <div id="para-que-serve" className={styles.root}>
      <div>
        <Typography variant="h1" color="purple" weight="bold">
          Para que serve?
        </Typography>
      </div>
      <div className={styles.subtitle}>
        <Typography weight="bold">
          A Covidografia rene informao, em tempo real, sobre os sintomas dos
          portugueses para melhor se avaliar a propagao da pandemia
        </Typography>
      </div>
      <div className={styles.content}>
        <div className={styles.column}>
          <Typography color="gray" variant="mediumBody" weight="medium">
            Esta recolha permite aos utilizadores da plataforma{" "}
            <strong>
              acompanhar, em tempo real, os sintomas de outros utilizadores na
              mesma rea geogrfica
            </strong>
            , com base em atualizaes feitas pelos prprios.
            <br />
            <br />
            Os utilizadores devem indicar, de forma regular, quais os seus
            sintomas, em particular aqueles que esto associados ao COVID-19.
          </Typography>
        </div>
        <div className={styles.column}>
          <Typography color="gray" variant="mediumBody" weight="medium">
            Respeitando sempre a privacidade dos utilizadores, esta informao
            ser agregada e partilhada com as autoridades de sade, que podero
            depois utilizar esta informao para tomarem decises que ajudem a
            proteger os cidados desta pandemia.
            <br />
            <br />A privacidade dos dados recolhidos  tratada com o mximo
            cuidado. Leia os nossos{" "}
            <a
              className={styles.link}
              href={`${data.site.siteMetadata.appSiteUrl}/#/terms-conditions`}
            >
              Termos &amp; Condies
            </a>
            .
          </Typography>
        </div>

        <img className={styles.image} src={image} alt="Para que serve" />
      </div>
    </div>
  );
};

export default ParaQueServe;
